@mixin reset-text {
  font-family: $font-family-base;
  // We deliberately do NOT reset font-size or word-wrap.
  font-style: normal;
  font-weight: $font-weight-normal;
  line-height: $line-height-base;
  text-align: left; // Fallback for where `start` is not supported
  text-align: start; // stylelint-disable-line declaration-block-no-duplicate-properties
  text-decoration: none;
  text-shadow: none;
  text-transform: none;
  letter-spacing: normal;
  word-break: normal;
  word-spacing: normal;
  white-space: normal;
  line-break: auto;
}

@mixin font-face($font-name, $file-name, $weight: normal, $style: normal) {
  @font-face {
    font-family: quote($font-name);
    font-style: $style;
    font-weight: $weight;
    src: url($file-name + ".eot") format("embedded-opentype"), url($file-name + ".woff") format("woff"), url($file-name + ".ttf") format("truetype"), url($file-name + ".svg##{$font-name}") format("svg");
  }
}

@mixin base-font-family() {
  font-family: $font-family-base;
}

@mixin font-shorthand($size: $font-size-base, $weight: $font-weight-base, $lineHeight: $line-height-base) {
  font-size: $size;
  font-weight: $weight;
  line-height: $lineHeight;
}

@mixin dynamic-font($size: $font-size-base, $dynamicSize: "-apple-system-body", $weight: $font-weight-base, $lineHeight: $line-height-base, $fontFamily: $font-family-base) {
  font: $dynamicSize;
  font-family: $fontFamily;
  font-size: $size;
  font-weight: $weight;
  line-height: $lineHeight;
}

@mixin font-base($size: $font-size-base, $weight: $font-weight-base, $lineHeight: $line-height-base, $fontFamily: $font-family-base) {
  font-family: $fontFamily;
  @include font-shorthand($size, $weight, $lineHeight);
}

@mixin hyphens($mode: auto) {
  hyphens: $mode;
  word-wrap: break-word;
}
