// Generates properties what use css variables

@mixin css-variable($property, $color, $var: null, $important:"") {

  #{$property}: $color unquote($important);
  @if ($var) {
    @if $enable-css-variables {
      @supports ( (--a: 0)) {
        #{$property}: var(#{$var}) unquote($important);
      }
    }
  }
}
